import React, { useCallback, useEffect, useState } from 'react';

import { Button, Card, ConfirmDialog, Dropdown, InputNumber, InputText, Loading, Modal, Toast } from '@memobit/libs';

import { medicalApi } from '@api/medical';

import { TestCategory, TestType, TestTypeSaveDTO } from '../../../types/medical';

import './Settings.scss';

interface SettingsProps {
    onBack: () => void;
}

interface EditTestTypeModalState {
    isOpen: boolean;
    mode: 'add' | 'edit';
    testType: Partial<TestType>;
}

interface DeleteConfirmState {
    isOpen: boolean;
    testType: TestType | null;
}

const Settings: React.FC<SettingsProps> = ({ onBack }) => {
    const [testTypes, setTestTypes] = useState<TestType[]>([]);
    const [filteredTestTypes, setFilteredTestTypes] = useState<TestType[]>([]);
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);
    const [error, setError] = useState<string>('');

    // Filters
    const [categoryFilter, setCategoryFilter] = useState<TestCategory | 'all'>('all');
    const [searchFilter, setSearchFilter] = useState<string>('');
    const [activeFilter, setActiveFilter] = useState<'all' | 'active' | 'inactive'>('all');

    // Modals
    const [editModal, setEditModal] = useState<EditTestTypeModalState>({
        isOpen: false,
        mode: 'add',
        testType: {},
    });
    const [deleteConfirm, setDeleteConfirm] = useState<DeleteConfirmState>({
        isOpen: false,
        testType: null,
    });

    const [toast, setToast] = useState<{ message: string; type: 'success' | 'error' } | null>(null);

    const categoryLabels: { [key in TestCategory]: string } = {
        hematologie: 'Hematologie',
        lipide: 'Lipide',
        metabolic: 'Metabolic',
        hepatic: 'Hepatic',
        renal: 'Renal',
        hormonal: 'Hormonal',
    };

    const categoryOptions = Object.entries(categoryLabels).map(([value, label]) => ({
        value: value as TestCategory,
        label,
        searchText: label,
    }));

    useEffect(() => {
        loadTestTypes();
    }, []);

    const loadTestTypes = async () => {
        try {
            setLoading(true);
            setError('');

            const response = await medicalApi.loadTestTypes();

            if (response.success) {
                setTestTypes(response.data);
            } else {
                setError(response.error || 'Eroare la ncrcarea tipurilor de analize');
            }
        } catch (err) {
            setError('Eroare la ncrcarea datelor');
            console.error('Error loading test types:', err);
        } finally {
            setLoading(false);
        }
    };

    const filterTestTypes = useCallback(() => {
        let filtered = [...testTypes];

        // Category filter
        if (categoryFilter !== 'all') {
            filtered = filtered.filter(tt => tt.category === categoryFilter);
        }

        // Search filter
        if (searchFilter.trim()) {
            const search = searchFilter.toLowerCase();
            filtered = filtered.filter(tt => tt.name.toLowerCase().includes(search));
        }

        setFilteredTestTypes(filtered);
    }, [categoryFilter, searchFilter, testTypes]);

    useEffect(() => {
        filterTestTypes();
    }, [testTypes, categoryFilter, searchFilter, activeFilter, filterTestTypes]);

    const handleAddTestType = () => {
        setEditModal({
            isOpen: true,
            mode: 'add',
            testType: {
                name: '',
                category: 'hematologie',
                defaultUnit: '',
                optimalRange: { min: 0, max: 100 },
            },
        });
    };

    const handleEditTestType = (testType: TestType) => {
        setEditModal({
            isOpen: true,
            mode: 'edit',
            testType: { ...testType },
        });
    };

    const handleSaveTestType = async () => {
        const { testType, mode } = editModal;

        // Validation
        if (!testType.name?.trim()) {
            setToast({ message: 'Numele analizei este obligatoriu', type: 'error' });
            return;
        }

        if (!testType.defaultUnit?.trim()) {
            setToast({ message: 'Unitatea de msur este obligatorie', type: 'error' });
            return;
        }

        if (!testType.optimalRange || testType.optimalRange.min >= testType.optimalRange.max) {
            setToast({ message: 'Intervalul optim este invalid', type: 'error' });
            return;
        }

        // Check for duplicate names (except when editing the same item)
        const duplicateName = testTypes.find(tt => tt.name.toLowerCase() === testType.name!.toLowerCase() && (mode === 'add' || tt.id !== testType.id));

        if (duplicateName) {
            setToast({ message: 'Exist deja o analiz cu acest nume', type: 'error' });
            return;
        }

        try {
            setSaving(true);

            const saveData: TestTypeSaveDTO = {
                name: testType.name!.trim(),
                category: testType.category!,
                defaultUnit: testType.defaultUnit!.trim(),
                optimalRange: testType.optimalRange!,
            };

            let response;
            if (mode === 'add') {
                response = await medicalApi.saveTestType(saveData);
            } else {
                response = await medicalApi.updateTestType(testType.id!, saveData);
            }

            if (response.success) {
                setToast({
                    message: mode === 'add' ? 'Analiza a fost adugat' : 'Analiza a fost actualizat',
                    type: 'success',
                });
                setEditModal({ isOpen: false, mode: 'add', testType: {} });
                await loadTestTypes(); // Refresh data
            } else {
                setToast({
                    message: response.error || 'Eroare la salvarea analizei',
                    type: 'error',
                });
            }
        } catch (err) {
            setToast({
                message: 'Eroare la salvarea analizei',
                type: 'error',
            });
        } finally {
            setSaving(false);
        }
    };

    const handleDeleteTestType = (testType: TestType) => {
        setDeleteConfirm({
            isOpen: true,
            testType,
        });
    };

    const confirmDeleteTestType = async () => {
        const { testType } = deleteConfirm;
        if (!testType) return;

        try {
            setSaving(true);

            const response = await medicalApi.deleteTestType(testType.id);

            if (response.success) {
                setToast({
                    message: 'Analiza a fost tears',
                    type: 'success',
                });
                setDeleteConfirm({ isOpen: false, testType: null });
                await loadTestTypes(); // Refresh data
            } else {
                setToast({
                    message: response.error || 'Eroare la tergerea analizei',
                    type: 'error',
                });
            }
        } catch (err) {
            setToast({
                message: 'Eroare la tergerea analizei',
                type: 'error',
            });
        } finally {
            setSaving(false);
        }
    };

    const updateEditModalField = (field: keyof TestType, value: any) => {
        setEditModal(prev => ({
            ...prev,
            testType: {
                ...prev.testType,
                [field]: value,
            },
        }));
    };

    const updateOptimalRange = (field: 'min' | 'max', value: number) => {
        setEditModal(prev => ({
            ...prev,
            testType: {
                ...prev.testType,
                optimalRange: {
                    ...prev.testType.optimalRange!,
                    [field]: value,
                },
            },
        }));
    };

    if (loading) {
        return (
            <div className="settings">
                <div className="settings__loading">
                    <Loading />
                    <p>Se ncarc setrile...</p>
                </div>
            </div>
        );
    }

    if (error) {
        return (
            <div className="settings">
                <div className="settings__error">
                    <h2>Eroare</h2>
                    <p>{error}</p>
                    <Button onClick={loadTestTypes}>Rencearc</Button>
                </div>
            </div>
        );
    }

    return (
        <div className="settings">
            {/* Header */}
            <div className="settings__header">
                <div className="settings__header-left">
                    <Button variant="secondary" size="small" onClick={onBack}>
                         napoi
                    </Button>
                    <div className="settings__title">
                        <h1>Setri Analize Medicale</h1>
                        <p>Gestioneaz tipurile de analize i intervalele optime</p>
                    </div>
                </div>
                <div className="settings__header-right">
                    <Button variant="primary" onClick={handleAddTestType}>
                        + Adaug Tip Analiz
                    </Button>
                </div>
            </div>

            {/* Filters */}
            <div className="settings__filters">
                <Card>
                    <div className="settings__filters-content">
                        <div className="settings__filter-group">
                            <label>Categorie:</label>
                            <select
                                value={categoryFilter}
                                onChange={e => setCategoryFilter(e.target.value as TestCategory | 'all')}
                                className="settings__select"
                            >
                                <option value="all">Toate categoriile</option>
                                {Object.entries(categoryLabels).map(([value, label]) => (
                                    <option key={value} value={value}>
                                        {label}
                                    </option>
                                ))}
                            </select>
                        </div>

                        <div className="settings__filter-group">
                            <label>Caut:</label>
                            <InputText value={searchFilter} onChange={setSearchFilter} placeholder="Nume analiz sau cuvnt cheie..." />
                        </div>

                        <div className="settings__filter-group">
                            <label>Status:</label>
                            <select
                                value={activeFilter}
                                onChange={e => setActiveFilter(e.target.value as 'all' | 'active' | 'inactive')}
                                className="settings__select"
                            >
                                <option value="all">Toate</option>
                                <option value="active">Doar active</option>
                                <option value="inactive">Doar inactive</option>
                            </select>
                        </div>

                        <div className="settings__filter-results">
                            Gsite: <strong>{filteredTestTypes.length}</strong> din {testTypes.length} analize
                        </div>
                    </div>
                </Card>
            </div>

            {/* Test Types List */}
            <div className="settings__content">
                <Card>
                    <h3>Tipuri de Analize ({filteredTestTypes.length})</h3>

                    {filteredTestTypes.length === 0 ? (
                        <div className="settings__no-results">
                            <p>Nu au fost gsite analize care s corespund filtrelor selectate.</p>
                            {(categoryFilter !== 'all' || searchFilter || activeFilter !== 'all') && (
                                <Button
                                    variant="secondary"
                                    onClick={() => {
                                        setCategoryFilter('all');
                                        setSearchFilter('');
                                        setActiveFilter('all');
                                    }}
                                >
                                    Reseteaz filtrele
                                </Button>
                            )}
                        </div>
                    ) : (
                        <div className="settings__test-types">
                            {filteredTestTypes.map(testType => (
                                <div key={testType.id} className="settings__test-type">
                                    <div className="settings__test-type-main">
                                        <div className="settings__test-type-info">
                                            <div className="settings__test-type-header">
                                                <h4>{testType.name}</h4>
                                                <div className="settings__test-type-badges">
                                                    <span className={`settings__category-badge ${testType.category}`}>{categoryLabels[testType.category]}</span>
                                                </div>
                                            </div>

                                            <div className="settings__test-type-details">
                                                <div className="settings__detail">
                                                    <span className="settings__detail-label">Unitate:</span>
                                                    <span className="settings__detail-value">{testType.defaultUnit}</span>
                                                </div>
                                                <div className="settings__detail">
                                                    <span className="settings__detail-label">Interval optim:</span>
                                                    <span className="settings__detail-value">
                                                        {testType.optimalRange.min} - {testType.optimalRange.max} {testType.defaultUnit}
                                                    </span>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="settings__test-type-actions">
                                            <Button variant="secondary" size="small" onClick={() => handleEditTestType(testType)} disabled={saving}>
                                                Editeaz
                                            </Button>
                                            <Button variant="danger" size="small" onClick={() => handleDeleteTestType(testType)} disabled={saving}>
                                                terge
                                            </Button>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </Card>
            </div>

            {/* Edit/Add Modal */}
            {editModal.isOpen && (
                <Modal
                    onClose={() => setEditModal({ isOpen: false, mode: 'add', testType: {} })}
                    title={editModal.mode === 'add' ? 'Adaug Tip Analiz' : 'Editeaz Tip Analiz'}
                    size="large"
                >
                    <div className="settings__edit-modal">
                        <div className="settings__form-group">
                            <label>Nume analiz *</label>
                            <InputText
                                value={editModal.testType.name || ''}
                                onChange={value => updateEditModalField('name', value)}
                                placeholder="ex: Hemoglobin"
                            />
                        </div>

                        <div className="settings__form-row">
                            <div className="settings__form-group">
                                <label>Categorie *</label>
                                <Dropdown
                                    name="settings-category"
                                    options={categoryOptions}
                                    value={editModal.testType.category || 'hematologie'}
                                    onChange={value => updateEditModalField('category', value)}
                                />
                            </div>

                            <div className="settings__form-group">
                                <label>Unitate msur *</label>
                                <InputText
                                    value={editModal.testType.defaultUnit || ''}
                                    onChange={value => updateEditModalField('defaultUnit', value)}
                                    placeholder="ex: g/dL, mg/dL, %"
                                />
                            </div>
                        </div>

                        <div className="settings__form-row">
                            <div className="settings__form-group">
                                <label>Valoare minim optim *</label>
                                <InputNumber
                                    value={editModal.testType.optimalRange?.min || 0}
                                    onChange={value => updateOptimalRange('min', value || 0)}
                                    step={0.1}
                                    min={0}
                                />
                            </div>

                            <div className="settings__form-group">
                                <label>Valoare maxim optim *</label>
                                <InputNumber
                                    value={editModal.testType.optimalRange?.max || 100}
                                    onChange={value => updateOptimalRange('max', value || 100)}
                                    step={0.1}
                                    min={editModal.testType.optimalRange?.min || 0}
                                />
                            </div>
                        </div>

                        <div className="settings__form-actions">
                            <Button variant="secondary" onClick={() => setEditModal({ isOpen: false, mode: 'add', testType: {} })} disabled={saving}>
                                Anuleaz
                            </Button>
                            <Button variant="primary" onClick={handleSaveTestType} disabled={saving} loading={saving}>
                                {saving ? 'Se salveaz...' : editModal.mode === 'add' ? 'Adaug' : 'Salveaz'}
                            </Button>
                        </div>
                    </div>
                </Modal>
            )}

            {/* Delete Confirmation */}
            <ConfirmDialog
                isOpen={deleteConfirm.isOpen}
                title="Confirm tergerea"
                message={`Eti sigur c vrei s tergi analiza "${deleteConfirm.testType?.name}"? Aceast aciune nu poate fi anulat.`}
                confirmText="terge"
                cancelText="Anuleaz"
                onConfirm={confirmDeleteTestType}
                onCancel={() => setDeleteConfirm({ isOpen: false, testType: null })}
                type="danger"
            />

            {/* Toast notifications */}
            {toast && <Toast message={toast.message} type={toast.type} onClose={() => setToast(null)} />}
        </div>
    );
};

export default Settings;
