import { TestResult, TestType } from '../types/medical';

/**
 * Determines if a test result value is within the optimal range
 */
export const getValueStatus = (value: number, optimalRange: { min: number; max: number }): 'optimal' | 'high' | 'low' => {
    if (value > optimalRange.max) return 'high';
    if (value < optimalRange.min) return 'low';
    return 'optimal';
};

/**
 * Gets the most recent result for each test type
 */
export const getLatestResultsPerTestType = (results: TestResult[], testTypes: TestType[]): { [testTypeId: string]: TestResult } => {
    const latest: { [testTypeId: string]: TestResult } = {};

    testTypes.forEach(testType => {
        const testResults = results.filter(r => r.testTypeId === testType.id).sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());

        if (testResults.length > 0) {
            latest[testType.id] = testResults[0];
        }
    });

    return latest;
};
