import React, { useState } from 'react';

import { Button } from '@memobit/libs';

import Dashboard from './Dashboard/Dashboard';
import Settings from './Settings/Settings';
import TestDetailsView from './TestDetails/TestDetailsView';

import './MedicalApp.scss';

type ViewType = 'dashboard' | 'testDetails' | 'settings';

export const MedicalApp: React.FC = () => {
    const [currentView, setCurrentView] = useState<ViewType>('dashboard');
    const [selectedTestTypeId, setSelectedTestTypeId] = useState<string | null>(null);

    const handleChartClick = (testTypeId: string) => {
        setSelectedTestTypeId(testTypeId);
        setCurrentView('testDetails');
    };

    const handleBackToDashboard = () => {
        setCurrentView('dashboard');
        setSelectedTestTypeId(null);
    };

    const handleOpenSettings = () => {
        setCurrentView('settings');
    };

    return (
        <div className="medical-app">
            {/* Navigation Header */}
            <div className="medical-app__header">
                <div className="medical-app__header-left">
                    <h1 className="medical-app__title">Analize Medicale</h1>
                    <p className="medical-app__subtitle">Monitorizarea sntii pe termen lung</p>
                </div>
                <div className="medical-app__header-right">
                    {currentView !== 'dashboard' && (
                        <Button variant="secondary" size="small" onClick={handleBackToDashboard}>
                             Dashboard
                        </Button>
                    )}
                    {currentView === 'dashboard' && (
                        <Button variant="secondary" onClick={handleOpenSettings}>
                             Setri
                        </Button>
                    )}
                </div>
            </div>

            {/* Main Content */}
            <div className="medical-app__content">
                {currentView === 'dashboard' && <Dashboard onChartClick={handleChartClick} />}

                {currentView === 'testDetails' && selectedTestTypeId && <TestDetailsView testTypeId={selectedTestTypeId} onBack={handleBackToDashboard} />}

                {currentView === 'settings' && <Settings onBack={handleBackToDashboard} />}
            </div>
        </div>
    );
};
