import React, { useEffect, useState } from 'react';

import { Button, Card, Dropdown, DropdownOption, Loading } from '@memobit/libs';

import AddResultsModal from '@components/medical/AddResultsModal/AddResultsModal';
import { ChartContainer, ChartDataPoint } from '@components/medical/ChartContainer/ChartContainer';
import { medicalApi } from '@api/medical';

import { TestCategory, TestResult, TestType } from '../../../types/medical';

import './Dashboard.scss';

interface DashboardData {
    categories: {
        [key in TestCategory]: {
            testTypes: TestType[];
            latestResults: TestResult[];
        };
    };
    recentActivity: TestResult[];
    problematicValues: Array<{
        result: TestResult;
        testType: TestType;
        deviation: 'high' | 'low';
    }>;
}

interface CategoryFilter {
    [key: string]: boolean;
}

interface DashboardProps {
    onChartClick?: (testTypeId: string) => void;
}

const Dashboard: React.FC<DashboardProps> = ({ onChartClick }) => {
    const [dashboardData, setDashboardData] = useState<DashboardData | null>(null);
    const [testTypes, setTestTypes] = useState<TestType[]>([]);
    const [testResults, setTestResults] = useState<TestResult[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string>('');
    const [categoryFilters, setCategoryFilters] = useState<CategoryFilter>({
        hematologie: true,
        lipide: true,
        metabolic: true,
        hepatic: true,
        renal: true,
        hormonal: true,
    });
    const [showAddModal, setShowAddModal] = useState(false);
    const [dateFilter, setDateFilter] = useState<{
        type: 'preset' | 'specific';
        preset?: string;
        specificDate?: string;
    }>({
        type: 'preset',
        preset: 'all',
    });
    const [availableDates, setAvailableDates] = useState<string[]>([]);

    const categoryLabels: { [key in TestCategory]: string } = {
        hematologie: 'Hematologie',
        lipide: 'Lipide',
        metabolic: 'Metabolic',
        hepatic: 'Hepatic',
        renal: 'Renal',
        hormonal: 'Hormonal',
    };

    useEffect(() => {
        loadDashboardData();
    }, []);

    useEffect(() => {
        if (testResults.length > 0) {
            const uniqueDates = [...new Set(testResults.map(result => new Date(result.date).toISOString().split('T')[0]))].sort().reverse(); // Cele mai recente primul
            setAvailableDates(uniqueDates);
        }
    }, [testResults]);

    // Funcie pentru filtrarea rezultatelor
    const getFilteredResults = (): TestResult[] => {
        if (dateFilter.type === 'preset' && dateFilter.preset === 'all') {
            return testResults;
        }

        const now = new Date();
        let startDate: Date;

        if (dateFilter.type === 'preset') {
            switch (dateFilter.preset) {
                case 'month':
                    startDate = new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);
                    break;
                case '3months':
                    startDate = new Date(now.getTime() - 90 * 24 * 60 * 60 * 1000);
                    break;
                case 'year':
                    startDate = new Date(now.getTime() - 365 * 24 * 60 * 60 * 1000);
                    break;
                default:
                    return testResults;
            }
            return testResults.filter(result => new Date(result.date) >= startDate);
        } else {
            // Specific date filter
            const targetDate = dateFilter.specificDate;
            return testResults.filter(result => new Date(result.date).toISOString().split('T')[0] === targetDate);
        }
    };

    const loadDashboardData = async () => {
        try {
            setLoading(true);
            setError('');

            const [dashboardResponse, testTypesResponse, testResultsResponse] = await Promise.all([
                medicalApi.loadDashboardData(),
                medicalApi.loadTestTypes(),
                medicalApi.loadTestResults(),
            ]);

            if (dashboardResponse.success) {
                setDashboardData(dashboardResponse.data);
            } else {
                setError(dashboardResponse.error);
            }

            if (testTypesResponse.success) {
                setTestTypes(testTypesResponse.data);
            }

            if (testResultsResponse.success) {
                setTestResults(testResultsResponse.data);
            }
        } catch (err) {
            setError('Eroare la ncrcarea datelor');
            console.error('Dashboard loading error:', err);
        } finally {
            setLoading(false);
        }
    };

    const getChartDataForTestType = (testTypeId: string): ChartDataPoint[] => {
        const filtered = getFilteredResults();
        return filtered
            .filter(result => result.testTypeId === testTypeId)
            .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime())
            .map(result => ({
                date: result.date.toISOString(),
                value: result.value,
                label: result.clinicName,
            }));
    };

    const toggleCategoryFilter = (category: TestCategory) => {
        setCategoryFilters(prev => ({
            ...prev,
            [category]: !prev[category],
        }));
    };

    const getVisibleCategories = (): TestCategory[] => {
        return Object.keys(categoryFilters)
            .filter(category => categoryFilters[category])
            .filter(category => {
                const categoryData = dashboardData?.categories[category as TestCategory];
                return (categoryData?.testTypes?.length || 0) > 0;
            }) as TestCategory[];
    };

    const handleChartClick = (testTypeId: string) => {
        if (onChartClick) {
            onChartClick(testTypeId);
        }
    };

    const handleAnalysisDaySelection = (type: 'preset' | 'specific', newSelection: DropdownOption | DropdownOption[] | null) => {
        if (typeof newSelection === 'object' && !Array.isArray(newSelection) && newSelection) {
            setDateFilter({ type, specificDate: newSelection?.value.toString() });
        }
    };

    if (loading) {
        return (
            <div className="dashboard">
                <div className="dashboard__loading">
                    <Loading />
                    <p>Se ncarc datele medicale...</p>
                </div>
            </div>
        );
    }

    if (error) {
        return (
            <div className="dashboard">
                <div className="dashboard__error">
                    <h2>Eroare</h2>
                    <p>{error}</p>
                    <Button onClick={loadDashboardData}>Rencearc</Button>
                </div>
            </div>
        );
    }

    if (!dashboardData) {
        return (
            <div className="dashboard">
                <div className="dashboard__no-data">
                    <h2>Nu sunt date disponibile</h2>
                    <p>Adaug primele tale analize medicale pentru a ncepe monitorizarea.</p>
                    <Button onClick={() => setShowAddModal(true)}>Adaug Analize</Button>
                </div>
            </div>
        );
    }

    return (
        <div className="dashboard">
            {/* Header */}
            <div className="dashboard__header">
                <div className="dashboard__title">
                    <h1>Dashboard Medical</h1>
                    <p>Monitorizarea analizelor medicale pe termen lung</p>
                </div>
                <div className="dashboard__actions">
                    <Button variant="primary" onClick={() => setShowAddModal(true)}>
                        + Adaug Rezultate
                    </Button>
                </div>
            </div>

            {/* Category Filters */}
            <div className="dashboard__filters">
                <h3>Categorii</h3>
                <div className="dashboard__category-filters">
                    {Object.entries(categoryLabels).map(([category, label]) => (
                        <button
                            key={category}
                            className={`dashboard__category-filter ${categoryFilters[category] ? 'active' : ''}`}
                            onClick={() => toggleCategoryFilter(category as TestCategory)}
                        >
                            {label}
                            {dashboardData.categories[category as TestCategory]?.testTypes.length > 0 && (
                                <span className="dashboard__category-count">{dashboardData.categories[category as TestCategory].testTypes.length}</span>
                            )}
                        </button>
                    ))}
                </div>
            </div>

            <div className="dashboard__date-filters">
                <h3>Perioada</h3>
                <div className="dashboard__date-filter-controls">
                    <div className="dashboard__preset-filters">
                        {[
                            { key: 'month', label: 'Ultima lun' },
                            { key: '3months', label: 'Ultimele 3 luni' },
                            { key: 'year', label: 'Ultimul an' },
                            { key: 'all', label: 'Toate datele' },
                        ].map(preset => (
                            <Button
                                key={preset.key}
                                className={`dashboard__date-filter ${dateFilter.type === 'preset' && dateFilter.preset === preset.key ? 'active' : ''}`}
                                onClick={() => setDateFilter({ type: 'preset', preset: preset.key, specificDate: undefined })}
                            >
                                {preset.label}
                            </Button>
                        ))}
                    </div>

                    <div className="dashboard__specific-date">
                        <label>Sau selecteaz o zi anume:</label>
                        <Dropdown
                            key={`analyses-available-dates`}
                            name="analyses-available-dates"
                            options={availableDates.map(date => ({
                                value: date,
                                label: new Date(date).toLocaleDateString('ro-RO', {
                                    weekday: 'short',
                                    day: 'numeric',
                                    month: 'short',
                                    year: 'numeric',
                                }),
                                searchText: date,
                            }))}
                            value={dateFilter.type === 'specific' ? dateFilter.specificDate || '' : ''}
                            onChange={newSelection => handleAnalysisDaySelection('specific', newSelection)}
                            placeholder="Selecteaz data..."
                            searchable
                        />
                    </div>
                </div>
            </div>

            {/* Quick Stats */}
            <div className="dashboard__stats">
                <Card className="dashboard__stat">
                    <h4>Total Analize</h4>
                    <span className="dashboard__stat-value">{testResults.length}</span>
                </Card>
                <Card className="dashboard__stat">
                    <h4>Valori Problematice</h4>
                    <span className={`dashboard__stat-value ${dashboardData.problematicValues.length > 0 ? 'warning' : ''}`}>
                        {dashboardData.problematicValues.length}
                    </span>
                </Card>
                <Card className="dashboard__stat">
                    <h4>Ultima Actualizare</h4>
                    <span className="dashboard__stat-value">
                        {dashboardData.recentActivity.length > 0 ? new Date(dashboardData.recentActivity[0].date).toLocaleDateString('ro-RO') : 'N/A'}
                    </span>
                </Card>
            </div>

            {/* Charts Grid */}
            <div className="dashboard__content">
                {getVisibleCategories().map(category => {
                    // Filter test types to only those with data
                    const testTypesWithData = dashboardData.categories[category].testTypes.filter(testType => {
                        const chartData = getChartDataForTestType(testType.id);
                        return chartData.length > 0;
                    });

                    // Only render the category if it has test types with data
                    if (testTypesWithData.length === 0) return null;

                    return (
                        <div key={category} className="dashboard__category">
                            <h2 className="dashboard__category-title">{categoryLabels[category]}</h2>
                            <div className="dashboard__charts-grid">
                                {testTypesWithData.map(testType => {
                                    const chartData = getChartDataForTestType(testType.id);
                                    return (
                                        <div key={testType.id} className="dashboard__chart-container" onClick={() => handleChartClick(testType.id)}>
                                            <ChartContainer
                                                data={chartData}
                                                title={testType.name}
                                                unit={testType.defaultUnit}
                                                optimalRange={testType.optimalRange}
                                                isMini={true}
                                                height={120}
                                                showGrid={false}
                                                showLegend={false}
                                            />
                                        </div>
                                    );
                                })}
                            </div>
                        </div>
                    );
                })}
            </div>

            {/* Problematic Values Alert */}
            {dashboardData.problematicValues.length > 0 && (
                <div className="dashboard__alerts">
                    <Card className="dashboard__alert">
                        <h3> Valori care necesit atenie</h3>
                        <div className="dashboard__problematic-list">
                            {dashboardData.problematicValues.slice(0, 5).map(({ result, testType, deviation }) => (
                                <div key={result.id} className="dashboard__problematic-item">
                                    <span className="dashboard__problematic-test">{testType.name}</span>
                                    <span className={`dashboard__problematic-value ${deviation}`}>
                                        {result.value}
                                        {testType.defaultUnit}
                                        {deviation === 'high' ? ' ' : ' '}
                                    </span>
                                    <span className="dashboard__problematic-date">{new Date(result.date).toLocaleDateString('ro-RO')}</span>
                                </div>
                            ))}
                        </div>
                        {dashboardData.problematicValues.length > 5 && (
                            <p className="dashboard__see-more">+{dashboardData.problematicValues.length - 5} mai multe...</p>
                        )}
                    </Card>
                </div>
            )}

            {/* Recent Activity */}
            <div className="dashboard__recent">
                <Card>
                    <h3>Activitate Recent</h3>
                    <div className="dashboard__recent-list">
                        {dashboardData.recentActivity.map(result => {
                            const testType = testTypes.find(tt => tt.id === result.testTypeId);
                            return (
                                <div key={result.id} className="dashboard__recent-item">
                                    <span className="dashboard__recent-test">{testType?.name || 'Test necunoscut'}</span>
                                    <span className="dashboard__recent-value">
                                        {result.value}
                                        {testType?.defaultUnit}
                                    </span>
                                    <span className="dashboard__recent-clinic">{result.clinicName}</span>
                                    <span className="dashboard__recent-date">{new Date(result.date).toLocaleDateString('ro-RO')}</span>
                                </div>
                            );
                        })}
                    </div>
                </Card>
            </div>

            {showAddModal && (
                <AddResultsModal
                    isOpen={showAddModal}
                    onClose={() => setShowAddModal(false)}
                    onSuccess={() => {
                        loadDashboardData(); // Refresh data dup salvare
                        setShowAddModal(false);
                    }}
                />
            )}
        </div>
    );
};

export default Dashboard;
