// Test Results
export const loadTestResults = async (): Promise<ApiCallListTestResultsReturn> => await getApiCall('testresults');

export const loadTestResultsByType = async (testTypeId: string): Promise<ApiCallTestResultsByTypeReturn> =>
    await getApiCall(`medical/test-results/by-type/${testTypeId}`);

export const loadTestResultsByDateRange = async (startDate: string, endDate: string): Promise<ApiCallListTestResultsReturn> =>
    await getApiCall(`medical/test-results/date-range?start=${startDate}&end=${endDate}`);

export const saveTestResult = async (testResult: TestResultSaveDTO): Promise<ResponseCreate> => await postApiCall('medical/test-results/add', testResult);

export const saveBulkTestResults = async (bulkResults: BulkTestResultsSaveDTO): Promise<ResponseCreate> =>
    await postApiCall('medical/test-results/bulk-add', bulkResults);

export const updateTestResult = async (id: string, testResult: TestResultSaveDTO): Promise<ResponseCreate> =>
    await postApiCall(`medical/test-results/update/${id}`, testResult);

export const deleteTestResult = async (id: string): Promise<ResponseDelete> => await getApiCall(`medical/test-results/delete/${id}`);

