// Clinics
export const loadClinics = async (): Promise<ApiCallListClinicsReturn> => await getApiCall('medical/clinics');

export const searchClinics = async (query: string): Promise<ApiCallListClinicsReturn> =>
    await getApiCall(`medical/clinics/search?q=${encodeURIComponent(query)}`);

// Dashboard Data
export const loadDashboardData = async (): Promise<ApiCallDashboardDataReturn> => await getApiCall('medical/dashboard');

export const loadDashboardDataFiltered = async (dateRange?: { start: string; end: string }, categories?: string[]): Promise<ApiCallDashboardDataReturn> => {
    let url = 'medical/dashboard';
    const params = new URLSearchParams();

    if (dateRange) {
        params.append('startDate', dateRange.start);
        params.append('endDate', dateRange.end);
    }

    if (categories && categories.length > 0) {
        params.append('categories', categories.join(','));
    }

    if (params.toString()) {
        url += `?${params.toString()}`;
    }

    return await getApiCall(url);
};

