export const loadDashboardDataMock = async (): Promise<ApiCallDashboardDataReturn> => {
    return new Promise(resolve => {
        setTimeout(() => {
            // Group test types by category
            const categoriesData: { [key in TestCategory]: any } = {
                hematologie: { testTypes: [], latestResults: [] },
                lipide: { testTypes: [], latestResults: [] },
                metabolic: { testTypes: [], latestResults: [] },
                hepatic: { testTypes: [], latestResults: [] },
                renal: { testTypes: [], latestResults: [] },
                hormonal: { testTypes: [], latestResults: [] },
            };

            // Group test types by category
            mockTestTypes.forEach(testType => {
                categoriesData[testType.category].testTypes.push(testType);
            });

            // Get latest results for each test type
            mockTestTypes.forEach(testType => {
                const testResults = mockTestResults
                    .filter(result => result.testTypeId === testType.id)
                    .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());

                if (testResults.length > 0) {
                    categoriesData[testType.category].latestResults.push(testResults[0]);
                }
            });

            const latestResults = getLatestResultsPerTestType(mockTestResults, mockTestTypes);
            const problematicValues = Object.values(latestResults)
                .map(result => {
                    const testType = mockTestTypes.find(tt => tt.id === result.testTypeId);
                    if (!testType) return null;

                    const status = getValueStatus(result.value, testType.optimalRange);
                    if (status !== 'optimal') {
                        return { result, testType, deviation: status as 'high' | 'low' };
                    }
                    return null;
                })
                .filter(item => item !== null) as any[];

            // Recent activity (last 5 results)
            const recentActivity = [...mockTestResults].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime()).slice(0, 5);

            resolve({
                success: true,
                error: '',
                data: {
                    categories: categoriesData,
                    recentActivity,
                    problematicValues,
                },
            });
        }, 600);
    });
};

export const saveTestResultMock = async (testResult: TestResultSaveDTO): Promise<ResponseCreate> => {
    return new Promise(resolve => {
        setTimeout(() => {
            // Simulate validation
            if (!testResult.testTypeId || !testResult.value || !testResult.date) {
                resolve({
                    success: false,
                    error: 'Missing required fields',
                    message: 'Please fill in all required fields',
                });
                return;
            }

            // Simulate successful save
            resolve({
                success: true,
                error: '',
                message: 'Test result saved successfully',
                id: Math.floor(Math.random() * 10000), // Mock ID
            });
        }, 800);
    });
};

export const saveBulkTestResultsMock = async (bulkResults: BulkTestResultsSaveDTO): Promise<ResponseCreate> => {
    return new Promise(resolve => {
        setTimeout(() => {
            if (!bulkResults.results || bulkResults.results.length === 0) {
                resolve({
                    success: false,
                    error: 'No results to save',
                    message: 'Please add at least one test result',
                });
                return;
            }

            resolve({
                success: true,
                error: '',
                message: `Successfully saved ${bulkResults.results.length} test results`,
            });
        }, 1200);
    });
};

