import React, { useEffect, useState } from 'react';

import { Button, Dropdown, DropdownOption, InputNumber, InputText, Modal, Toast } from '@memobit/libs';

import { medicalApi } from '@api/medical';

import { BulkTestResultsSaveDTO, Clinic, TestType } from '../../../types/medical';

import './AddResultsModal.scss';

interface TestResultEntry {
    id: string;
    testTypeId: string;
    testTypeName: string;
    value: number | null;
    unit: string;
    notes?: string;
}

interface AddResultsModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

const AddResultsModal: React.FC<AddResultsModalProps> = ({ isOpen, onClose, onSuccess }) => {
    const [selectedDate, setSelectedDate] = useState<string>(new Date().toISOString().split('T')[0]);
    const [selectedClinic, setSelectedClinic] = useState<string>('');
    const [clinicSearch, setClinicSearch] = useState<string>('');
    const [testResults, setTestResults] = useState<TestResultEntry[]>([]);

    // Data
    const [testTypes, setTestTypes] = useState<TestType[]>([]);
    const [clinics, setClinics] = useState<Clinic[]>([]);
    const [filteredClinics, setFilteredClinics] = useState<Clinic[]>([]);

    // UI State
    const [loading, setLoading] = useState(false);
    const [saving, setSaving] = useState(false);
    const [error, setError] = useState<string>('');
    const [toast, setToast] = useState<{ message: string; type: 'success' | 'error' } | null>(null);

    useEffect(() => {
        if (isOpen) {
            loadInitialData();
            // Reset form when modal opens
            setTestResults([]);
            setSelectedClinic('');
            setClinicSearch('');
            setError('');
        }
    }, [isOpen]);

    useEffect(() => {
        // Filter clinics based on search
        if (clinicSearch.trim() === '') {
            setFilteredClinics(clinics.slice(0, 10)); // Show top 10 most used
        } else {
            const filtered = clinics.filter(clinic => clinic.name.toLowerCase().includes(clinicSearch.toLowerCase()));
            setFilteredClinics(filtered.slice(0, 10));
        }
    }, [clinicSearch, clinics]);

    const loadInitialData = async () => {
        try {
            setLoading(true);
            setError('');

            const [testTypesResponse, clinicsResponse] = await Promise.all([medicalApi.loadTestTypes(), medicalApi.loadClinics()]);

            if (testTypesResponse.success) {
                setTestTypes(testTypesResponse.data);
            } else {
                setError('Eroare la ncrcarea tipurilor de analize');
            }

            if (clinicsResponse.success) {
                const sortedClinics = clinicsResponse.data.sort((a, b) => b.usageCount - a.usageCount);
                setClinics(sortedClinics);
                setFilteredClinics(sortedClinics.slice(0, 10));
            }
        } catch (err) {
            setError('Eroare la ncrcarea datelor');
            console.error('Error loading modal data:', err);
        } finally {
            setLoading(false);
        }
    };

    const addTestResult = () => {
        const newEntry: TestResultEntry = {
            id: `temp_${Date.now()}`,
            testTypeId: '',
            testTypeName: '',
            value: null,
            unit: '',
            notes: '',
        };
        setTestResults(prev => [...prev, newEntry]);
    };

    const removeTestResult = (id: string) => {
        setTestResults(prev => prev.filter(entry => entry.id !== id));
    };

    const updateTestResult = (id: string, updates: Partial<TestResultEntry>) => {
        setTestResults(prev => prev.map(entry => (entry.id === id ? { ...entry, ...updates } : entry)));
    };

    const handleTestTypeSelect = (entryId: string, newSelection: DropdownOption | DropdownOption[] | null) => {
        if (typeof newSelection === 'object' && !Array.isArray(newSelection) && newSelection) {
            const testType = testTypes.find(tt => tt.id === newSelection.value.toString());

            if (testType) {
                updateTestResult(entryId, {
                    testTypeId: testType.id,
                    testTypeName: testType.name,
                    unit: testType.defaultUnit,
                });
            }
        }
    };

    const handleClinicSelect = (newSelection: DropdownOption | DropdownOption[] | null) => {
        if (typeof newSelection === 'object' && !Array.isArray(newSelection) && newSelection) {
            setSelectedClinic(newSelection.value.toString());
            setClinicSearch(newSelection.value.toString());
        }
    };

    const handleClinicSearchChange = (value: string) => {
        setClinicSearch(value);
        setSelectedClinic(value);
    };

    const validateForm = (): string | null => {
        if (!selectedDate) {
            return 'Selecteaz data analizei';
        }

        if (!selectedClinic.trim()) {
            return 'Introdu numele clinicii';
        }

        if (testResults.length === 0) {
            return 'Adaug cel puin o analiz';
        }

        for (const result of testResults) {
            if (!result.testTypeId) {
                return 'Selecteaz tipul analizei pentru toate nregistrrile';
            }
            if (result.value === null || result.value === undefined) {
                return 'Introdu valoarea pentru toate analizele';
            }
            if (result.value < 0) {
                return 'Valorile nu pot fi negative';
            }
        }

        // Check for duplicates
        const testTypeIds = testResults.map(r => r.testTypeId);
        const duplicates = testTypeIds.filter((id, index) => testTypeIds.indexOf(id) !== index);
        if (duplicates.length > 0) {
            return 'Nu poi aduga acelai tip de analiz de mai multe ori pentru aceeai dat';
        }

        return null;
    };

    const handleSave = async () => {
        const validationError = validateForm();
        if (validationError) {
            setError(validationError);
            return;
        }

        try {
            setSaving(true);
            setError('');

            const bulkData: BulkTestResultsSaveDTO = {
                date: selectedDate,
                clinicName: selectedClinic.trim(),
                results: testResults.map(entry => ({
                    testTypeId: entry.testTypeId,
                    value: entry.value!,
                    notes: entry.notes?.trim() || undefined,
                })),
            };

            const response = await medicalApi.saveBulkTestResults(bulkData);

            if (response.success) {
                setToast({
                    message: response.message || 'Analizele au fost salvate cu succes',
                    type: 'success',
                });

                // Close modal and refresh parent data after a short delay
                setTimeout(() => {
                    onSuccess();
                    onClose();
                }, 1500);
            } else {
                setError(response.error || 'Eroare la salvarea analizelor');
            }
        } catch (err) {
            setError('Eroare la salvarea analizelor');
            console.error('Error saving test results:', err);
        } finally {
            setSaving(false);
        }
    };

    const getAvailableTestTypes = () => {
        const selectedTestTypeIds = testResults.map(r => r.testTypeId).filter(Boolean);
        return testTypes.filter(tt => !selectedTestTypeIds.includes(tt.id));
    };

    const formatTestTypeOptions = (testTypesArray: TestType[]) => {
        return testTypesArray.map(testType => ({
            value: testType.id,
            label: `${testType.name} (${testType.defaultUnit})`,
            searchText: `${testType.name}`,
        }));
    };

    const formatClinicOptions = () => {
        return filteredClinics.map(clinic => ({
            value: clinic.name,
            label: clinic.name,
            searchText: clinic.name,
        }));
    };

    if (!isOpen) return null;

    return (
        <>
            {isOpen && (
                <Modal onClose={onClose} title="Adaug Rezultate Analize" size="large" className="add-results-modal">
                    <div className="add-results-modal__content">
                        {loading ? (
                            <div className="add-results-modal__loading">
                                <p>Se ncarc datele...</p>
                            </div>
                        ) : (
                            <>
                                {/* Error Display */}
                                {error && <div className="add-results-modal__error">{error}</div>}

                                {/* Basic Information */}
                                <div className="add-results-modal__basic-info">
                                    <h3>Informaii de baz</h3>
                                    <div className="add-results-modal__form-row">
                                        <div className="add-results-modal__form-group">
                                            <label htmlFor="analysis-date">Data analizei *</label>
                                            <input
                                                id="analysis-date"
                                                type="date"
                                                value={selectedDate}
                                                onChange={e => setSelectedDate(e.target.value)}
                                                max={new Date().toISOString().split('T')[0]}
                                                className="add-results-modal__date-input"
                                            />
                                        </div>
                                        <div className="add-results-modal__form-group">
                                            <label htmlFor="clinic-name">Clinica *</label>
                                            <Dropdown
                                                name="clinic-name"
                                                options={formatClinicOptions()}
                                                value={selectedClinic}
                                                onChange={handleClinicSelect}
                                                placeholder="Caut sau selecteaz clinica..."
                                                searchable
                                                searchValue={clinicSearch}
                                                onSearchChange={handleClinicSearchChange}
                                                allowCustomValue
                                                className="add-results-modal__clinic-dropdown"
                                            />
                                        </div>
                                    </div>
                                </div>

                                {/* Test Results */}
                                <div className="add-results-modal__test-results">
                                    <div className="add-results-modal__section-header">
                                        <h3>Rezultate analize</h3>
                                        <Button variant="secondary" size="small" onClick={addTestResult} disabled={saving}>
                                            + Adaug analiz
                                        </Button>
                                    </div>

                                    {testResults.length === 0 ? (
                                        <div className="add-results-modal__no-tests">
                                            <p>Nu ai adugat nc nicio analiz.</p>
                                            <Button onClick={addTestResult}>Adaug prima analiz</Button>
                                        </div>
                                    ) : (
                                        <div className="add-results-modal__test-list">
                                            {testResults.map((entry, index) => (
                                                <div key={entry.id} className="add-results-modal__test-entry">
                                                    <div className="add-results-modal__test-header">
                                                        <span className="add-results-modal__test-number">#{index + 1}</span>
                                                        <Button variant="danger" size="small" onClick={() => removeTestResult(entry.id)} disabled={saving}>
                                                            terge
                                                        </Button>
                                                    </div>

                                                    <div className="add-results-modal__test-fields">
                                                        <div className="add-results-modal__form-group">
                                                            <label>Tip analiz *</label>
                                                            <Dropdown
                                                                name="analysis-type"
                                                                options={formatTestTypeOptions(getAvailableTestTypes())}
                                                                value={entry.testTypeId}
                                                                onChange={value => handleTestTypeSelect(entry.id, value)}
                                                                placeholder="Caut i selecteaz analiza..."
                                                                searchable
                                                                disabled={saving}
                                                            />
                                                        </div>

                                                        <div className="add-results-modal__form-group">
                                                            <label>Valoare *</label>
                                                            <div className="add-results-modal__value-input">
                                                                <InputNumber
                                                                    value={entry.value || undefined}
                                                                    onChange={value => updateTestResult(entry.id, { value })}
                                                                    placeholder="0.00"
                                                                    step={0.01}
                                                                    min={0}
                                                                    disabled={saving}
                                                                />
                                                                <span className="add-results-modal__unit">{entry.unit || 'unitate'}</span>
                                                            </div>
                                                        </div>

                                                        <div className="add-results-modal__form-group">
                                                            <label>Observaii</label>
                                                            <InputText
                                                                value={entry.notes || ''}
                                                                onChange={value => updateTestResult(entry.id, { notes: value })}
                                                                placeholder="Observaii opionale..."
                                                                disabled={saving}
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            ))}
                                        </div>
                                    )}
                                </div>

                                {/* Actions */}
                                <div className="add-results-modal__actions">
                                    <Button variant="secondary" onClick={onClose} disabled={saving}>
                                        Anuleaz
                                    </Button>
                                    <Button variant="primary" onClick={handleSave} disabled={saving || testResults.length === 0} loading={saving}>
                                        {saving ? 'Se salveaz...' : `Salveaz ${testResults.length} analize`}
                                    </Button>
                                </div>
                            </>
                        )}
                    </div>
                </Modal>
            )}

            {/* Toast notifications */}
            {toast && <Toast message={toast.message} type={toast.type} onClose={() => setToast(null)} />}
        </>
    );
};

export default AddResultsModal;
