import React, { useCallback, useEffect, useMemo, useState } from 'react';

import { Button, Card, ConfirmDialog, InputNumber, Loading, Modal, Toast } from '@memobit/libs';

import { ChartContainer, ChartDataPoint } from '@components/medical/ChartContainer';
import { medicalApi } from '@api/medical';

import { TestResult, TestType } from '../../../types/medical';

import './TestDetailsView.scss';

interface TestDetailsViewProps {
    testTypeId: string;
    onBack: () => void;
}

interface DateRange {
    start: string;
    end: string;
    label: string;
}

interface EditRangeModalState {
    isOpen: boolean;
    min: number;
    max: number;
}

interface DeleteConfirmState {
    isOpen: boolean;
    resultId: string | null;
    resultValue?: string;
    resultDate?: string;
}

const TestDetailsView: React.FC<TestDetailsViewProps> = ({ testTypeId, onBack }) => {
    const [testType, setTestType] = useState<TestType | null>(null);
    const [testResults, setTestResults] = useState<TestResult[]>([]);
    const [filteredResults, setFilteredResults] = useState<TestResult[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string>('');
    const [selectedDateRange, setSelectedDateRange] = useState<DateRange | null>(null);
    const [editRangeModal, setEditRangeModal] = useState<EditRangeModalState>({
        isOpen: false,
        min: 0,
        max: 0,
    });
    const [toast, setToast] = useState<{ message: string; type: 'success' | 'error' } | null>(null);
    const [deleteConfirm, setDeleteConfirm] = useState<DeleteConfirmState>({
        isOpen: false,
        resultId: null,
    });

    const dateRanges: DateRange[] = useMemo(
        () => [
            {
                start: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
                end: new Date().toISOString().split('T')[0],
                label: 'Ultima lun',
            },
            {
                start: new Date(Date.now() - 90 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
                end: new Date().toISOString().split('T')[0],
                label: 'Ultimele 3 luni',
            },
            {
                start: new Date(Date.now() - 365 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
                end: new Date().toISOString().split('T')[0],
                label: 'Ultimul an',
            },
            {
                start: new Date(2020, 0, 1).toISOString().split('T')[0],
                end: new Date().toISOString().split('T')[0],
                label: 'Toate datele',
            },
        ],
        []
    );

    const loadTestData = useCallback(async () => {
        try {
            setLoading(true);
            setError('');

            const [testTypesResponse, testResultsResponse] = await Promise.all([medicalApi.loadTestTypes(), medicalApi.loadTestResults()]);

            if (testTypesResponse.success) {
                const foundTestType = testTypesResponse.data.find(tt => tt.id === testTypeId);
                if (foundTestType) {
                    setTestType(foundTestType);
                } else {
                    setError('Tipul de analiz nu a fost gsit');
                    return;
                }
            } else {
                setError('Eroare la ncrcarea tipului de analiz');
                return;
            }

            if (testResultsResponse.success) {
                const relevantResults = testResultsResponse.data
                    .filter(result => result.testTypeId === testTypeId)
                    .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());

                setTestResults(relevantResults);

                // Set default date range based on data availability
                if (relevantResults.length > 0) {
                    const oldestDate = new Date(relevantResults[0].date);
                    const now = new Date();
                    const monthsAgo = (now.getTime() - oldestDate.getTime()) / (1000 * 60 * 60 * 24 * 30);

                    if (monthsAgo <= 1) {
                        setSelectedDateRange(dateRanges[0]); // Last month
                    } else if (monthsAgo <= 3) {
                        setSelectedDateRange(dateRanges[1]); // Last 3 months
                    } else if (monthsAgo <= 12) {
                        setSelectedDateRange(dateRanges[2]); // Last year
                    } else {
                        setSelectedDateRange(dateRanges[3]); // All data
                    }
                }
            } else {
                setError('Eroare la ncrcarea rezultatelor');
            }
        } catch (err) {
            setError('Eroare la ncrcarea datelor');
            console.error('Error loading test data:', err);
        } finally {
            setLoading(false);
        }
    }, [dateRanges, testTypeId]);

    const filterResults = useCallback(() => {
        if (!selectedDateRange) {
            setFilteredResults(testResults);
            return;
        }

        const startDate = new Date(selectedDateRange.start);
        const endDate = new Date(selectedDateRange.end);

        const filtered = testResults.filter(result => {
            const resultDate = new Date(result.date);
            return resultDate >= startDate && resultDate <= endDate;
        });

        setFilteredResults(filtered);
    }, [selectedDateRange, testResults]);

    useEffect(() => {
        loadTestData();
    }, [loadTestData, testTypeId]);

    useEffect(() => {
        filterResults();
    }, [testResults, selectedDateRange, filterResults]);

    const getChartData = (): ChartDataPoint[] => {
        return filteredResults.map(result => ({
            date: result.date.toISOString(),
            value: result.value,
            label: `${result.clinicName} - ${new Date(result.date).toLocaleDateString('ro-RO')}`,
        }));
    };

    const getStatistics = () => {
        if (filteredResults.length === 0) return null;

        const values = filteredResults.map(r => r.value);
        const min = Math.min(...values);
        const max = Math.max(...values);
        const avg = values.reduce((sum, val) => sum + val, 0) / values.length;
        const latest = filteredResults[filteredResults.length - 1];

        let trend: 'up' | 'down' | 'stable' = 'stable';
        if (filteredResults.length >= 2) {
            const previous = filteredResults[filteredResults.length - 2];
            const change = ((latest.value - previous.value) / previous.value) * 100;
            if (change > 5) trend = 'up';
            else if (change < -5) trend = 'down';
        }

        return { min, max, avg, latest, trend };
    };

    const getValueStatus = (value: number): 'optimal' | 'high' | 'low' => {
        if (!testType) return 'optimal';

        if (value > testType.optimalRange.max) return 'high';
        if (value < testType.optimalRange.min) return 'low';
        return 'optimal';
    };

    const handleEditRange = () => {
        if (testType) {
            setEditRangeModal({
                isOpen: true,
                min: testType.optimalRange.min,
                max: testType.optimalRange.max,
            });
        }
    };

    const handleSaveRange = async () => {
        if (!testType) return;

        try {
            const response = await medicalApi.updateOptimalRange(testType.id, {
                min: editRangeModal.min,
                max: editRangeModal.max,
            });

            if (response.success) {
                setTestType({
                    ...testType,
                    optimalRange: {
                        min: editRangeModal.min,
                        max: editRangeModal.max,
                    },
                });
                setEditRangeModal({ ...editRangeModal, isOpen: false });
                setToast({
                    message: 'Intervalul optim a fost actualizat',
                    type: 'success',
                });
            } else {
                setToast({
                    message: response.error || 'Eroare la actualizarea intervalului',
                    type: 'error',
                });
            }
        } catch (err) {
            setToast({
                message: 'Eroare la actualizarea intervalului',
                type: 'error',
            });
        }
    };

    const handleDeleteResult = (resultId: string) => {
        // Gsete rezultatul pentru detalii n confirm dialog
        const result = filteredResults.find(r => r.id === resultId);

        setDeleteConfirm({
            isOpen: true,
            resultId,
            resultValue: result ? `${result.value}${testType?.defaultUnit}` : '',
            resultDate: result ? new Date(result.date).toLocaleDateString('ro-RO') : '',
        });
    };

    const confirmDeleteResult = async () => {
        const { resultId } = deleteConfirm;
        if (!resultId) return;

        try {
            const response = await medicalApi.deleteTestResult(resultId);

            if (response.success) {
                setTestResults(prev => prev.filter(r => r.id !== resultId));
                setToast({
                    message: 'Rezultatul a fost ters',
                    type: 'success',
                });
            } else {
                setToast({
                    message: response.error || 'Eroare la tergerea rezultatului',
                    type: 'error',
                });
            }
        } catch (err) {
            setToast({
                message: 'Eroare la tergerea rezultatului',
                type: 'error',
            });
        } finally {
            setDeleteConfirm({ isOpen: false, resultId: null });
        }
    };

    if (loading) {
        return (
            <div className="test-details">
                <div className="test-details__loading">
                    <Loading />
                    <p>Se ncarc detaliile analizei...</p>
                </div>
            </div>
        );
    }

    if (error || !testType) {
        return (
            <div className="test-details">
                <div className="test-details__error">
                    <h2>Eroare</h2>
                    <p>{error || 'Analiza nu a fost gsit'}</p>
                    <Button onClick={onBack}>napoi la Dashboard</Button>
                </div>
            </div>
        );
    }

    const statistics = getStatistics();

    return (
        <div className="test-details">
            {/* Header */}
            <div className="test-details__header">
                <div className="test-details__header-left">
                    <Button variant="secondary" size="small" onClick={onBack}>
                         napoi
                    </Button>
                    <div className="test-details__title">
                        <h1>{testType.name}</h1>
                        <p>Istoric complet al analizei</p>
                    </div>
                </div>
                <div className="test-details__header-right">
                    <Button onClick={handleEditRange}>Editeaz interval optim</Button>
                </div>
            </div>

            {/* Date Range Filters */}
            <div className="test-details__filters">
                <h3>Perioada afiat</h3>
                <div className="test-details__date-ranges">
                    {dateRanges.map((range, index) => (
                        <button
                            key={index}
                            className={`test-details__date-range ${selectedDateRange?.label === range.label ? 'active' : ''}`}
                            onClick={() => setSelectedDateRange(range)}
                        >
                            {range.label}
                        </button>
                    ))}
                </div>
            </div>

            {/* Statistics Cards */}
            {statistics && (
                <div className="test-details__stats">
                    <Card className="test-details__stat">
                        <h4>Ultima valoare</h4>
                        <div className={`test-details__stat-value ${getValueStatus(statistics.latest.value)}`}>
                            {statistics.latest.value}
                            {testType.defaultUnit}
                            <span className="test-details__stat-trend">
                                {statistics.trend === 'up' && ''}
                                {statistics.trend === 'down' && ''}
                                {statistics.trend === 'stable' && ''}
                            </span>
                        </div>
                        <span className="test-details__stat-date">{new Date(statistics.latest.date).toLocaleDateString('ro-RO')}</span>
                    </Card>

                    <Card className="test-details__stat">
                        <h4>Valoare medie</h4>
                        <div className={`test-details__stat-value ${getValueStatus(statistics.avg)}`}>
                            {statistics.avg.toFixed(2)}
                            {testType.defaultUnit}
                        </div>
                        <span className="test-details__stat-subtitle">n perioada selectat</span>
                    </Card>

                    <Card className="test-details__stat">
                        <h4>Interval</h4>
                        <div className="test-details__stat-range">
                            <span className={getValueStatus(statistics.min)}>
                                {statistics.min}
                                {testType.defaultUnit}
                            </span>
                            <span className="test-details__stat-separator">-</span>
                            <span className={getValueStatus(statistics.max)}>
                                {statistics.max}
                                {testType.defaultUnit}
                            </span>
                        </div>
                        <span className="test-details__stat-subtitle">min - max</span>
                    </Card>

                    <Card className="test-details__stat">
                        <h4>Total msurtori</h4>
                        <div className="test-details__stat-value">{filteredResults.length}</div>
                        <span className="test-details__stat-subtitle">n perioada selectat</span>
                    </Card>
                </div>
            )}

            {/* Chart */}
            <div className="test-details__chart-section">
                <Card>
                    <div className="test-details__chart-header">
                        <h3>Evoluia n timp</h3>
                        <div className="test-details__optimal-range">
                            Interval optim: {testType.optimalRange.min} - {testType.optimalRange.max} {testType.defaultUnit}
                        </div>
                    </div>

                    {filteredResults.length > 0 ? (
                        <ChartContainer
                            data={getChartData()}
                            title={`${testType.name} - ${selectedDateRange?.label || 'Toate datele'}`}
                            unit={testType.defaultUnit}
                            optimalRange={testType.optimalRange}
                            height={400}
                            showGrid={true}
                            showLegend={true}
                        />
                    ) : (
                        <div className="test-details__no-data">
                            <p>Nu exist date pentru perioada selectat</p>
                            <Button onClick={() => setSelectedDateRange(dateRanges[3])}>Vezi toate datele</Button>
                        </div>
                    )}
                </Card>
            </div>

            {/* Results Table */}
            <div className="test-details__table-section">
                <Card>
                    <h3>Toate rezultatele ({filteredResults.length})</h3>
                    {filteredResults.length > 0 ? (
                        <div className="test-details__table">
                            <div className="test-details__table-header">
                                <span>Data</span>
                                <span>Valoare</span>
                                <span>Status</span>
                                <span>Clinic</span>
                                <span>Observaii</span>
                                <span>Aciuni</span>
                            </div>
                            <div className="test-details__table-body">
                                {[...filteredResults].reverse().map(result => (
                                    <div key={result.id} className="test-details__table-row">
                                        <span className="test-details__table-date">{new Date(result.date).toLocaleDateString('ro-RO')}</span>
                                        <span className="test-details__table-value">
                                            {result.value}
                                            {testType.defaultUnit}
                                        </span>
                                        <span className={`test-details__table-status ${getValueStatus(result.value)}`}>
                                            {getValueStatus(result.value) === 'optimal' && ' Optim'}
                                            {getValueStatus(result.value) === 'high' && ' Ridicat'}
                                            {getValueStatus(result.value) === 'low' && ' Sczut'}
                                        </span>
                                        <span className="test-details__table-clinic">{result.clinicName}</span>
                                        <span className="test-details__table-notes">{result.notes || '-'}</span>
                                        <span className="test-details__table-actions">
                                            <Button variant="danger" size="small" onClick={() => handleDeleteResult(result.id)}>
                                                terge
                                            </Button>
                                        </span>
                                    </div>
                                ))}
                            </div>
                        </div>
                    ) : (
                        <div className="test-details__no-results">
                            <p>Nu exist rezultate n perioada selectat</p>
                        </div>
                    )}
                </Card>
            </div>

            {/* Edit Range Modal */}
            {editRangeModal.isOpen && (
                <Modal onClose={() => setEditRangeModal({ ...editRangeModal, isOpen: false })} title="Editeaz intervalul optim" size="small">
                    <div className="test-details__edit-range">
                        <p>
                            Definete intervalul optim pentru <strong>{testType.name}</strong>:
                        </p>

                        <div className="test-details__range-inputs">
                            <div className="test-details__range-input">
                                <label>Valoare minim:</label>
                                <InputNumber
                                    value={editRangeModal.min}
                                    onChange={value => setEditRangeModal(prev => ({ ...prev, min: value || 0 }))}
                                    step={0.1}
                                    min={0}
                                />
                                <span>{testType.defaultUnit}</span>
                            </div>

                            <div className="test-details__range-input">
                                <label>Valoare maxim:</label>
                                <InputNumber
                                    value={editRangeModal.max}
                                    onChange={value => setEditRangeModal(prev => ({ ...prev, max: value || 0 }))}
                                    step={0.1}
                                    min={editRangeModal.min}
                                />
                                <span>{testType.defaultUnit}</span>
                            </div>
                        </div>

                        <div className="test-details__range-actions">
                            <Button variant="secondary" onClick={() => setEditRangeModal({ ...editRangeModal, isOpen: false })}>
                                Anuleaz
                            </Button>
                            <Button variant="primary" onClick={handleSaveRange} disabled={editRangeModal.min >= editRangeModal.max}>
                                Salveaz
                            </Button>
                        </div>
                    </div>
                </Modal>
            )}

            {/* Toast notifications */}
            {toast && <Toast message={toast.message} type={toast.type} onClose={() => setToast(null)} />}

            <ConfirmDialog
                isOpen={deleteConfirm.isOpen}
                title="Confirm tergerea rezultatului"
                message={`Eti sigur c vrei s tergi rezultatul ${deleteConfirm.resultValue} din data de ${deleteConfirm.resultDate}? Aceast aciune nu poate fi anulat.`}
                confirmText="terge"
                cancelText="Anuleaz"
                onConfirm={confirmDeleteResult}
                onCancel={() => setDeleteConfirm({ isOpen: false, resultId: null })}
                type="danger"
            />
        </div>
    );
};

export default TestDetailsView;
